:root {
    --brand: #cd2c2c;
    --action: #90a955;
    --white: white;
    --base-light: #ecf39e;
    --base-dark: #4caf50;
    --black: #0a2342;
    --font-default: "Delima SemiBold", sans-serif;
    --space-small: 8px;
    --space-medium: 16px;
    --space-large: 32px;
    --space-xl: 64px;
}

* {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

/* Overlay covers entire screen */
.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    backdrop-filter: blur(4px);
    overflow: hidden;
}

/* Popup content fills full screen */
.popup-content {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90vw;
    max-width: 1600px;
    max-height: 90vh;
    background-color: #fff;
    padding: 32px;
    box-sizing: border-box;
    animation: fadeIn 0.3s ease-in-out;
    overflow-y: auto;
    border-radius: 16px; /* or any value you prefer */
}

/* Header styling */
.popup-content h2 {
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 1.5rem;
    color: #333;
}

body.popup-open {
    overflow: hidden;
}

/* Grid layout */
.grid-container {
    flex-grow: 1;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 15px;
    padding: var(--space-medium);
    overflow-y: auto;
    height: 100%; /* Make it fill the available height */
    box-sizing: border-box;
}

.grid-item {
    background-color: #f9f9f9;
    border: 1px solid #ccc;
    padding: 10px;
    text-align: center;
    font-size: 1rem;
    border-radius: 4px;
    word-wrap: break-word;
    overflow-wrap: break-word;
    white-space: normal;
}

/* Form fields */
.comment-box,
.signoff-date,
.signoff-name {
    width: 100%;
    margin-top: 6px;
    padding: 8px;
    font-size: 14px;
}

/* Close button */
.close-button {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    background: none;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.2s ease;
}

.close-button:hover {
    transform: scale(1.1);
}

.close-button svg {
    width: 100%;
    height: 100%;
}

/* Progress bar */
.progress-bar-container {
    width: 100%;
    background-color: #e0e0e0;
    height: 25px;
    border-radius: 10px;
    margin-bottom: 20px;
    overflow: hidden;
    position: relative;
}

.progress-bar {
    height: 100%;
    background-color: #4caf50;
    transition: width 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: bold;
    font-size: 14px;
}

.progress-text {
    position: absolute;
    width: 100%;
    text-align: center;
    z-index: 1;
}

/* Custom checkbox */
.custom-checkbox {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 1rem;
    cursor: pointer;
    position: relative;
}

.checkmark {
    height: 20px;
    width: 20px;
    background-color: #e0e0e0;
    border-radius: 4px;
    position: relative;
    flex-shrink: 0;
}

.custom-checkbox input:checked ~ .checkmark {
    background-color: var(--base-dark);
}

.checkmark::after {
    content: "";
    position: absolute;
    display: none;
}

.custom-checkbox input:checked ~ .checkmark {
    background-color: #4caf50;
}

.custom-checkbox .checkmark::after {
    left: 6px;
    top: 2px;
    width: 6px;
    height: 12px;
    border: solid white;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}

input[type="checkbox"] {
    accent-color: var(--base-dark);
}

/* Animation */
@keyframes fadeIn {
    from { opacity: 0; transform: scale(0.95); }
    to { opacity: 1; transform: scale(1); }
}

/* Responsive layout */
@media (max-width: 768px) {
    .popup-content {
        width: 100vw;
        padding: 16px;
    }

    .grid-container {
        grid-template-columns: 1fr;
    }

    .grid-item {
        font-size: 0.9rem;
        padding: 8px;
    }

    .comment-box,
    .signoff-date,
    .signoff-name {
        font-size: 13px;
        padding: 6px;
    }

    .grid-item {
        font-size: 0.9rem;
        padding: 8px;
    }

    .comment-box,
    .signoff-date,
    .signoff-name {
        font-size: 13px;
        padding: 6px;
    }

    .close-button {
        width: 48px;
        height: 48px;
    }
}